import React from "react";
import styled from "styled-components";
import Title from "../atoms/Title";
import Paragraph from "../atoms/Paragraph";
import Button from "../atoms/Button";

const StyledWrapper = styled.div`
  margin: 0 10rem 4rem;
  position: relative;
  display: flex;
  min-height: 40vh;
`;

const BorderBox = styled.div`
  position: absolute;
  top: 1rem;
  left: 1rem;
  right: 1rem;
  bottom: 1rem;
  border: 3px solid ${({ theme }) => theme.orange};
`;

const StyledTextWrapper = styled.div`
  flex-basis: 50%;
  padding: 1.5rem;
  padding-left: ${({ reverse }) => (reverse ? "2.5rem" : "1.5rem")};
  padding-right: ${({ reverse }) => (reverse ? "1.5rem" : "2.5rem")};
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 10;
`;

const StyledImgWrapper = styled.div`
  flex-basis: 50%;
  background-image: url(${({ img }) => img});
  background-size: cover;
  background-position: center;
`;

const StyledSubtitle = styled(Title)`
  margin: 0.5rem 0 1rem;
`;

const StyledButton = styled(Button)`
  margin: 2rem 0 0;
`;

const PackageBox = ({ img, reverseOrder }) => {
  const componentsOrder = reverseOrder
    ? [
        <StyledTextWrapper reverse>
          <Title>Przykladowy pakiet</Title>
          <StyledSubtitle small orange>
            Podtytuł do pakietu
          </StyledSubtitle>
          <Paragraph>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam in
            imperdiet enim. In faucibus nisi et rhoncus finibus. Nulla auctor,
            magna at faucibus laoreet, quam sem rutrum elit, eget euismod eros
            libero at nunc. In tincidunt orci non nunc finibus interdum. Integer
            tristique ante in ultricies sagittis. Morbi tincidunt libero sem,
            fermentum pharetra leo porta id.
          </Paragraph>
          <StyledButton>Czytaj wiecej</StyledButton>
        </StyledTextWrapper>,
        <StyledImgWrapper img={img} />,
      ]
    : [
        <StyledImgWrapper img={img} />,
        <StyledTextWrapper>
          <Title>Przykladowy pakiet</Title>
          <StyledSubtitle small orange>
            Podtytul do pakietu
          </StyledSubtitle>
          <Paragraph>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam in
            imperdiet enim. In faucibus nisi et rhoncus finibus. Nulla auctor,
            magna at faucibus laoreet, quam sem rutrum elit, eget euismod eros
            libero at nunc. In tincidunt orci non nunc finibus interdum. Integer
            tristique ante in ultricies sagittis. Morbi tincidunt libero sem,
            fermentum pharetra leo porta id.
          </Paragraph>
          <StyledButton>Czytaj wiecej</StyledButton>
        </StyledTextWrapper>,
      ];

  return (
    <StyledWrapper>
      <BorderBox />
      {componentsOrder}
    </StyledWrapper>
  );
};

export default PackageBox;
