import React from "react";
import styled from "styled-components";
import contactImg from "../../assets/images/contact.jpg";
import Paragraph from "../atoms/Paragraph";
import Title from "../atoms/Title";

const StyledWrapper = styled.div`
  display: flex;
  margin: 3rem;
  box-shadow: 0 0 10px 10px ${({ theme }) => theme.black};
  min-height: 80vh;
`;

const StyledTextWrapper = styled.div`
  flex-basis: 40%;
  padding: 3rem 5rem 3rem 3rem;
`;

const StyledImgWrapper = styled.div`
  flex-grow: 1;
  background-image: url(${contactImg});
  background-size: cover;
  background-position: center;
  filter: grayscale(0.8);
  clip-path: polygon(40% 0, 100% 0, 100% 100%, 0 100%);
`;

const StyledTitle = styled(Title)`
  margin: 1rem 0 2rem;
`;

const StyledHeadTitle = styled(Title)`
  margin: 2rem 0 0;
  text-align: center;
`;

const AboutUsWrapper = () => (
  <>
    <StyledHeadTitle>Poznajmy sie lepiej</StyledHeadTitle>
    <StyledWrapper>
      <StyledTextWrapper>
        <Title>Kim jestesmy?</Title>
        <StyledTitle small orange>
          Kilka informacji o nas
        </StyledTitle>
        <Paragraph>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis
          tincidunt rutrum risus in sodales. Nam luctus ipsum egestas, fermentum
          est nec, bibendum tellus. Quisque ipsum ligula, interdum vel justo
          quis, tempor rutrum enim. Praesent ex urna, sodales a pulvinar et,
          sagittis vel ligula. Suspendisse sodales sem vel sem facilisis
          fermentum. Etiam nec scelerisque est, non placerat nibh. In hac
          habitasse platea dictumst. Curabitur ante eros, porttitor non enim
          vitae, efficitur finibus lectus. In sapien erat, placerat id felis
          vulputate, vulputate maximus erat. Maecenas vehicula eros sit amet
          diam rutrum, id porttitor augue vehicula. Suspendisse tincidunt, diam
          quis accumsan dignissim, magna eros rutrum nunc, nec sagittis ante
          lorem pretium ex. Mauris aliquet justo elit, non viverra enim accumsan
          quis. Mauris tristique turpis sit amet massa eleifend lacinia. Nam
          vulputate faucibus pretium. Donec rhoncus ornare urna placerat mollis.
          Aenean non massa laoreet, molestie quam nec, rutrum nisi.
        </Paragraph>
      </StyledTextWrapper>
      <StyledImgWrapper />
    </StyledWrapper>
  </>
);

export default AboutUsWrapper;
